@media (max-width: 1200px) {
    .menu {
        &_item {
            padding-right: 20px; }
        &_link {
            font-size: 13px; } }
    .promo {
        &_header {
            font-size: 50px; } }
    .mobile_item {
        width: 280px;
        height: 260px; }
    footer .social li {
        margin: 48px 6px 38px 10px; } }

@media (max-width:991px) {
    .menu {
        &_item {
            padding-right: 8px; }
        &_link {
            font-size: 11px; } }
    .subheader {
        &_logo {
            img {
                width: 100%; } }
        &_official {
            font-size: 11px; }
        &_phone {
            font-size: 18px; }
        &_btn {
            margin-top: 21px;
            width: 100%;
            height: 40px; } }
    .promo {
        &_header {
            font-size: 40px; }
        &_subheader {
            font-size: 25px; }
        &_descr {
            font-size: 14px; }
        &_btn {
            width: 35%;
            height: 40px; } }
    .reason {
        &_img {
            margin: auto 33.3%; }
        .icon_header {
            width: 100%;
            margin-top: 15px; }
        .icon_txt {
            width: 100%;
            font-size: 14px; } } }

@media (max-width:767px) {
    .menu {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background-color: rgba(0, 0, 0, .8);
        transition: 0.6s all;
        &_active {
            left: 0;
            z-index: 2; }
        &_item {
            padding-right: 0;
            border: none;
            margin: 10px auto; }
        &_link {
            font-size: 20px; } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 25px;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: #fff;
            margin-bottom: 2px;
            transition: 0.5s all; }
        &_active {
            span {
                margin-bottom: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }
    .xs-hidden {
        display: none; }
    .xs-visible {
        display: block; }
    .subheader {
        &_phone {
            display: block;
            margin-top: 10px;
            font-weight: 400;
            font-size: 16px;
            text-transform: uppercase;
            color: #FFFFFF;
            text-align: center; }
        &_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 10px; } }
    .promo {
        padding: 135px 0;
        &_header {
            line-height: 45px; } } }

@media (max-width:575px) {
    nav {
        height: 30px; }
    .subheader {
        height: 60px;
        &_official {
            display: none; }
        &_logo {
            margin-top: 18px; }
        &_phone {
            display: block;
            margin-top: 8px;
            font-weight: 400;
            font-size: 10px;
            text-transform: uppercase;
            color: #FFFFFF;
            text-align: center;
            margin-top: 7px; }
        &_btn {
            width: 110px;
            height: 22px;
            font-size: 10px;
            margin-top: 6px; } }
    .promo {
        min-height: 400px;
        padding: 93px 0 35px 0;
        &_descr {
            display: none; }
        &_header {
            font-size: 30px;
            padding: 0 24px;
            line-height: 35px; }
        &_subheader {
            font-size: 20px;
            line-height: 25px;
            margin-top: 33px;
            padding: 0 10px; }
        &_btn {
            width: 210px;
            height: 50px;
            font-size: 16px;
            margin-top: 72px; } }
    .menu {
        width: 85%;
        &_item {
            width: 100%;
            text-align: center;
            padding: 5px 0; }
        &_link {
            display: block; } }
    .reason {
        padding: 22px 0 36px 0;
        &_img {
            width: 100px;
            height: 100px; } }
    .reason .block {
        margin-top: 26px; }
    .title {
        font-size: 16px;
        margin-top: 15px;
        line-height: 16px; }
    .mobile {
        padding: 25px 0;
        &_item_subtitle {
            font-size: 20px; } }

    .auto {
        padding: 30px 0;
        min-height: 100%;
        .black {
            height: 400px; }
        .title {
            display: none; }
        .subtitle {
            display: none; }
        .img_auto {
            margin: 35px 0;
            width: 100%; }
        .text {
            position: relative;
            color: #fff;
            z-index: 3; } }
    .require {
        padding: 20px 0 30px 0;
        &_title {
            font-size: 16px;
            line-height: 24px; }
        &_list {
            margin-top: 5px;
            padding-left: 30px; }
        &_list li {
            font-size: 14px;
            line-height: 18px;
            margin-bottom: 2px; }
        &_descr {
            font-size: 14px;
            line-height: 16px; }
        &_block {
            margin: 15px; } }
    .world {
        height: 100%;
        padding: 26px 0 10px 0;
        .word_city {
            margin-top: 211px; } }

    .footer {
        flex-wrap: wrap;
        &_item {
            margin: 22px 33px; }
        &_wrapper {
            display: block;
            text-align: center; }
        .footer_wrapper > div:nth-child(3) {
            width: 90%;
            justify-content: center; }
        &_social {
            display: inline;
            width: 100%;
            padding-left: 0px;
            text-align: center;
            margin-bottom: 0px; }
        &_links {
            display: flex;
            flex-wrap: wrap;
            width: 100%;
            padding: 5px 4px;
            font-size: 14px;
            &_sub {
                font-size: 11px;
                line-height: 14px; } }

        &_mobile_app {
            display: inline-block;
            text-align: center;
            margin-top: 5px; } } }

