
head {
    position: relative; }

nav {
    height: 50px;
    background: #212121; }
a {
    text-decoration: none; }
.navbar {
    width: 100%;

    .container {
        justify-content: center; } }

.hamburger {
    display: none; }

.menu {

    display: flex;
    justify-content: space-between;
    margin: 0px;
    padding: 0 auto;
    list-style: none;

    &_link {
        font-weight: 300;
        font-size: 15px;
        color: #FFFFFF;
        padding-left: 14px; }

    &_item {
        border-right: 2px dotted black;
        padding-right: 20px;
        &:last-child {
            border-right: none; } } }

.subheader {
    position: absolute;
    width: 100%;
    height: 87px;
    background: rgba(0, 0, 0, 0.6);

    &_logo {
        display: block;
        max-width: 100%;
        margin-top: 16px; }

    &_official {
        margin-top: 5px;
        font-size: 13px;
        color: #FFFFFF; }

    &_call {
        margin-top: 20px;
        font-weight: 300;
        font-size: 12px;
        color: #FFFFFF;
        text-align: right; }

    &_phone {
        display: block;
        font-weight: 400;
        font-size: 22px;
        color: #FFFFFF;
        text-align: right; }

    &_btn {
        margin-top: 20px;
        width: 172px;
        height: 46px;
        background: linear-gradient(0deg, $main-color 0%, #4CE2FF 100%);
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.06);
        border: none;
        border-radius: 6px;
        font-weight: 400;
        font-size: 14px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        @include btn_hover; } }

.promo {
    min-height: 800px;
    background: url('../img/bg/uber_bg.jpg') center (center / cover) no-repeat;
    padding: 180px 0 135px 0;

    &_header {
        font-weight: 900;
        font-size: 60px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        line-height: 60px; }

    &_subheader {
        font-weight: 500;
        font-size: 36px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        line-height: 36px; }

    &_descr {
        margin-top: 20px;
        font-weight: 400;
        font-size: 18px;
        text-align: center;
        color: #FFFFFF; }

    &_btn {
        display: block;
        margin: 115px auto 0 auto;
        width: 259px;
        height: 63px;
        background: linear-gradient(0deg, $main-color 0%, #4CE2FF 100%);
        box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.06);
        border: none;
        border-radius: 6px;
        font-weight: 400;
        font-size: 18px;
        text-align: center;
        text-transform: uppercase;
        color: #FFFFFF;
        text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
        @include btn_hover; } }

.reason {
    // display: flex
    flex-wrap: wrap;
    justify-content: space-around;
    min-height: 1000px;
    padding-top: 18px; }

.label {
    position: relative;
    margin: 0 auto;
    width: 115px;
    height: 27px;
    background: #000000;
    font-weight: 400;
    font-size: 11px;
    line-height: 30px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;
    mix-blend-mode: normal;
    z-index: 1;
    &_white {
        background: #FFFFFF;
        color: #000000; } }

.title {
    position: relative;
    margin: 25px auto 16px;
    font-weight: 700;
    font-size: 38px;
    line-height: 48px;
    text-align: center;
    text-transform: uppercase;
    color: #222222;
    z-index: 1;
    &_white {
        color: #ffff; } }

.subtitle {
    position: relative;
    font-weight: 400;
    font-size: 17px;
    color: #222222;
    text-align: center;
    z-index: 2;
    &_white {
        color: #ffff;
        margin-bottom: 54px; } }

.reason_img {
    display: flex;
    float: left;
    margin: auto 25px; }

.icon_header {
    width: 50%;
    display: inline-block;
    flex-wrap: wrap;
    font-weight: 400;
    font-size: 17px;
    color: $main-color; }

.icon_txt {
    width: 50%;
    display: inline-block;
    margin-bottom: 26px; }

.mobile {
    min-height: 854px;
    padding: 48px 0 79px 0;
    background: url(../img/bg/mobile_app.png) center (center / cover) no-repeat;
    .row {
        margin-top: 32px; }
    &_item {
        position: relative;
        width: 320px;
        height: 280px;
        margin: 0 auto;
        margin-bottom: 19px;
        padding-top: 92px;
        &_1 {
            background-image: url(../img/kads/kad_1.png)/* center (center / cover) no-repeat */; }
        &_2 {
            background-image: url(../img/kads/kad_2.png)/* center (center / cover) no-repeat */; }
        &_3 {
            background-image: url(../img/kads/kad_3.png)/* center (center / cover) no-repeat */; }
        &_4 {
            background-image: url(../img/kads/kad_4.png)/* center (center / cover) no-repeat */; }
        &_5 {
            background-image: url(../img/kads/kad_5.png)/* center (center / cover) no-repeat */; }
        &_6 {
            background-image: url(../img/kads/kad_6.png)/* center (center / cover) no-repeat */; }
        &_subtitle {
            color: #ffffff;
            font-size: 24px;
            font-weight: 400;
            text-align: center;
            text-transform: uppercase;
            line-height: 31px; }
        &_plus {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            bottom: 35px;
            left: 50%;
            transform: translateX (-50%);
            width: 29px;
            height: 29px;
            margin: 0 auto;
            cursor: pointer;
            &::before {
                content: '';
                display: block;
                width: 14px;
                height: 2px;
                background-color: $main-color;
                transition: 0.4s all; }
            &::after {
                content: '';
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                display: block;
                width: 2px;
                height: 14px;
                background-color: $main-color;
                transition: 0.4s all; }
            &:hover {
                &::before, &::after {
                    background-color: $second-color; } } } } }

.auto {
    position: relative;
    min-height: 900px;
    padding: 59px 0 139px 0;
    background: url(../img/bg/mobile_app.png) center (center / cover) no-repeat;

    .img_auto {
        position: relative;
        display: block;
        margin: 56px auto 0 auto;
        z-index: 2; }
    .text {
        font-weight: 300;
        font-size: 17px;
        line-height: 24px;
        text-align: center;
        color: #222222; }
    .black {
        position: absolute;
        top: 0;
        left: 0;
        height: 525px;
        width: 100%;
        background-color: #070716;
        z-index: 1; } }

.require {
    padding: 68px 0 84px 0;
    &_block {
        margin: 33px 0;
        &_nmb {
            margin-bottom: 0; } }
    &_title {
        font-weight: 400;
        font-size: 22px;
        line-height: 24px;
        color: $main-color; }
    &_list {
        list-style-type: none;
        padding-left: 50px;
        margin-top: 22px;
        li {
            position: relative;
            font-weight: 300;
            font-size: 17px;
            line-height: 24px;
            color: #222222;
            margin-bottom: 10px;
            &::before {
                content: '';
                display: block;
                position: absolute;
                left: -32px;
                top: 3px;
                width: 21px;
                height: 20px;
                background: url('../icons/check_blue.png') center (center / cover) no-repeat; } } }
    &_descr {
        font-weight: 300;
        font-size: 17px;
        line-height: 24px;
        align-items: center;
        color: $main-color; }
    .warning {
        .require_title {
            color: $second-color; }
        .require_list {
            li {
                &::before {
                    background: url('../icons/check_red.png') center (center / cover) no-repeat; } } } } }

.world {
    height: 720px;
    padding: 48px 0 79px 0;
    background: url('../img/bg/world_bg.jpg') center (center / cover) no-repeat;
    a {
        display: block;
        text-align: center;
        margin-top: 456px;
        font-weight: 400;
        font-size: 15px;
        line-height: 68px;
        text-transform: uppercase;
        color: $main-color; } }

footer {
    padding: 14px 23px 39px 23px;
    a {
        &:hover {
            text-decoration: none; } } }

.footer {
    &_divider {
        width: 299px;
        height: 4px;
        background-color: #1fbad6;
        margin: 0 auto; }
    &_wrapper {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        padding-top: 32px;
        .footer_social {
            max-height: 50px;
            &_item {
                border: none;
                // border-radius: 6px
                img {
                    width: 32px;
                    background-color: black;
                    border: none;
                    border-radius: 100%;
                    margin-right: 5px; } } } }


    &_wrapper > div {
        width: 33%;
        &:nth-child(3) {
            display: flex;
            justify-content: flex-end; } }


    &_links {
        width: 390px;
        margin: 0 auto;
        &_main {
            display: flex;
            justify-content: space-between;
            padding: 0 10px;
            a {
                color: #070716;
                font-size: 15px;
                font-weight: 300;
                text-transform: uppercase; } }
        &_sub {
            display: flex;
            justify-content: space-between;
            margin-top: 10px;
            a {
                color: #999999;
                font-size: 11px;
                font-weight: 300;
                text-transform: uppercase; } }
        &_lang {
            display: block;
            margin: 0 auto;
            color: #1fbad6;
            font-size: 11px;
            font-weight: 400;
            margin-top: 19px;
            text-transform: uppercase;
            text-align: center; } }
    &_mobile_app {
        display: flex;
        justify-content: flex-end;
        img {
            margin-right: 10px; } } }

.xs-visible {
    display: none; }
